* {
    margin: 0;
    padding: 0;
    list-style: none;
}
/* @import url(&#39;
https://fonts.googleapis.com/css2?family=Poppins&amp;
display=swap&#39;
);
*/
body {
    font-family: sequel, sans-serif;
    cursor: none;
}
img {
    width: 100%;
}
.App {
    background: #e4e4e4;
    overflow: hidden;
}
.logo, nav {
    font-size: 20px;
    font-weight: 500;
    position: fixed;
    padding-top: 80px;
}
.logo {
    left: 100px;
}
nav {
    right: 100px;
    display: flex;
}
.les_deux:first-child {
    margin-right: 30px;
}
section {
    margin: 0 100px;
}
.header {
    padding-top: 350px;
    position: relative;
}
.header_centre {
    width: 50%;
    margin-left: 300px;
}
.header_centre .header_text {
    width: 50%;
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 250px;
}
.header_centre .header_img {
    position: relative;
    z-index: 2;
}
.header_centre .header_img img {
    width: 800px;
    height: 800px;
}
.header_scroll {
    position: relative;
}
.h1_box {
    position: absolute;
    top: 500px;
    font-size: 120px;
    font-weight: 900;
    text-transform: uppercase;
    margin: 0;
    line-height: 150%;
    margin-right: 100px;
    font-stretch: ultra-expanded;
}
.h1_haut {
    z-index: 1;
    position: relative;
}
.h1_bas {
    position: absolute;
    left: -50%;
    z-index: 2;
}
.bord {
    -webkit-text-stroke: 3px #484848;
    color: transparent;
}
.prese {
    width: 400px;
    margin-left: 300px;
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 150px;
    margin-top: 100px;
}
.titre {
    display: flex;
    flex-direction: column;
    font-size: 150px;
    font-weight: 900;
    text-transform: uppercase;
    margin: 0;
    line-height: 78%;
    margin-top: 150px;
}
.titre_travaux, .titre_hobby {
    text-align: right;
}
.projet {
    display: flex;
    margin-top: 100px;
}
.projet .desc, .projet .tecno {
    width: 50%;
}
.desc_img {
    width: 100%;
    height: 700px;
}
.miles {
    height: 600px;
}
.projet_text {
    display: flex;
    font-weight: 700;
    margin-top: 50px;
}
.projet_text p {
    font-size: 35px;
    margin-bottom: 20px;

}
.projet_text .nom {
    margin-left: 50px;
}
.tecno {
    position: relative;
    font-size: 25px;
}
.tecno ul {
    position: absolute;
    width: 100%;
    bottom: 10%;
    left: 0;
}
.tecno ul li {
    margin-top: 20px;
    border-bottom: 1px solid white;
}
.projet2 {
    flex-direction: row-reverse;
}
.projet2 .desc_img {
    width: 100%;
}
.projet2 ul {
    width: 90%;
    text-align: right;
}
.liste_arch {
    width: 70%;
    margin: auto;
    padding-top: 100px;
}
.liste_arch li {
    display: flex;
    font-size: 20px;
    align-items: center;
    justify-content: center;
    padding: 20px 0;
    border-bottom: 2px solid gray;
}
.liste_arch div {
    width: 25%;
}
.section_hobby {
    display: flex;
    flex-direction: row-reverse;
}
.hobby_img, .titre_hobby {
    width: 50%;
}
.titre_hobby {
    text-align: left;
}
.titre_hobby span {
    margin-left: -10px;
}
.hobby_img {
    margin-top: 350px;
    display: flex;
    justify-content: space-between;
}
.hobby1, .hobby2 {
    width: 50%;
}
.hobby1 {
    margin-right: 10px;
}
.hobby_box {
    position: relative;
}
.hobby_over {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: 0.5s;
    opacity: 0;
    font-size: 25px;
    font-weight: 500;
}
.hobby_over .over_text {
    color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.hobby_over:hover {
    opacity: 1;
    z-index: 10;
    background-color: rgba(0, 0, 0, 0.613);
}
.hobby1 {
    margin-top: 150px;
}
.hobby_text {
    font-size: 25px;
    text-transform: lowercase;
    font-weight: 400;
    line-height: normal;
    text-align: left;
    margin: 100px;
    width: 400px;
}
.section_contact {
    display: flex;
    flex-direction: row-reverse;
    height: 100vh;
}
.contact, .titre_contact {
    width: 50%;
    margin-top: 200px;
}
.titre_contact {
    text-align: right;
    position: relative;
    margin-left: -10px;
}
.bord2 {
    position: relative;
    z-index: 12;
}
.contact_img {
    width: 550px;
    height: 550px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
}
.contact_img img {
    width: 100%;
}
.contact {
    padding: 200px;
    font-size: 20px;
}
.contact p {
    margin-bottom: 100px;
}
.contact .me_contact {
    width: 100%;
    height: 70px;
    display: flex;
    align-items: center;
}
.contact .me_contact span {
    padding: 20px;
}


/* SOURIS */
.inner-cursor{
    position: fixed;
    left: 10px;
    width: 10px;
    height: 10px;
    transform: translate(-50%, -50%);
    background-color: #fff;
    mix-blend-mode: difference;
    border-radius: 50%;
    pointer-events: none;
    transition: width 0.5s, height 0.5s;
    z-index: 11;
}

.inner-cursor.grow{
    width: 30px;
    height: 30px;
    transition: width 0.5s, height 0.5s;
    z-index: 150;
}

.outer-cursor{
    position: fixed;
    left: 10px;
    width: 30px;
    height: 30px;
    transform: translate(-50%, -50%);
    border: 1px solid #fff;
    mix-blend-mode: difference;
    border-radius: 50%;
    pointer-events: none;
    transition: 0.2s;
}



